*{

    margin: 0;
    padding:0;
    text-decoration:none;
}
h2{

    font-size: 36px;
    margin-bottom: 15px;
    margin-top:30px
}

h1{

    font-size:45px;
    margin-bottom:25px
}
.results {
    display:flex;
    
    flex-direction:column;
/*    grid-template-columns: repeat(4, 1fr);*/
/*    max-width: 1200px;*/
    /*margin-left: auto;
    margin-right: auto;
    margin-top: 50px;*/
    padding: 60px 15px 60px 30px;
    background-color:bisque;
    min-height:1000px
}

.search {
    background-color: #f3d1a8;
    position: sticky;
    top: 0; 
    padding: 15px;
    z-index:999
}

.input {
    font-size: 25px;
   width:100%;
   text-align: center;
   border: 50%

}

div{

    line-height:2.1rem
}

.syn{

    padding-top:10px;
    font-size:21px
   
}

.meaning{
  margin-top:15px;
  border-top:2px solid black
}
.italic{

    font-style:italic;
    font-weight:bold;
    font-size:26px
}

.submit{
        font-size: 21px;
    padding: 5px 5px 2px;
}

form{
    margin:0 15px;
    display:flex;
    gap:10px;
}

.srcUrl{

    background-color: ;
    text-align: left;
    padding-left: 25px;
    padding-top: 25px;
    font-size:22px;
    color: black!important;
}

.srcUrl a{

    color:black;
}

.srcUrl a:hover{

    color:red;
}

.table{
overflow-x: auto;
text-align:center;
display: flex;
justify-content:center
}

.wikitable{
    overflow-x: auto;
    background-color: #f8f9fa;
    color: #202122;
    margin: 1em 0;
    border: 1px solid #a2a9b1!important;
    border-collapse: collapse;
}

td{

    border: 1px solid #a2a9b1;
    padding: 0.2em 0.4em;
}

.notesView {
    font-size: 18px;
    counter-reset: word;
}

.words {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
}

.notesView li {
    margin-bottom: .75em;
    position: relative;
    width: 100%;
    }


    .word {
    cursor: auto ;
    border: 0;
    box-shadow: none;
    margin: 0 0 10px 0;
    padding: 0;
    font-size: 32px;
    color: #020202;
    display: inline-block;
    font-weight:bold;
}

.definition {
    
    background-repeat: no-repeat;
    padding-left: 1.25em;
    background-position: left top 5px;
}

.details {
    font-size: 22px;
    margin: 1em 0 0 0;
    }
.details strong{
    margin-left:8px
}

.list{
    background-color:bisque;
    padding: 30px 30px 0;

} 

.list ul li{

    list-style-type:disc;
    font-size: 25px;
   
}   

.list ul{
    margin-left:20px
}
 










